<template>
  <div class="feed">
    <div class="title">
      <a :href="article.link" target="_blank">
        <h3>{{ article.title }}</h3>
      </a>
    </div>
    <div class="body">
      <b-img :src="this.getImg()" fluid></b-img>
      <p class="content" v-html="article.content"></p>
    </div>
    <div class="footer">
      <span class="hostname">{{ this.getHost() }}</span>
      <!-- <span v-if="article.isoDate" class="middot">&bull;</span>
      <span class="datetime">{{ this.getDate() }}</span> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  props: ["article"],
  methods: {
    getHost() {
      if (this.article) {
        try {
          const url = new URL(this.article.link);
          return url.hostname;
        } catch (e) {
          console.error(e.toString());
        }
      }
    },
    getDate() {
      if (this.article) {
        return this.article.isoDate;
      }
    },
    getImg() {
      if (this.article) {
        let str = this.article.enclosure.url
        if (str.indexOf('.jpg') != -1 ) 
          str = str.slice(0, str.indexOf('.jpg') + 4)
        else if (str.indexOf('.png') != -1 ) 
          str= str.slice(0, str.indexOf('.png') + 4)
        console.log(str)
        return str
      }
    }
  }
};
</script>

<style scoped>
h3 {
  margin: 0;
  padding: 0;
}
.feed {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  padding: 1.25rem;
  margin-bottom: 15px;
}
.body .content {
  margin-top: 5px;
}
.footer {
  font-size: 13px;
  color: #777;
  display: flex;
  align-items: flex-end;
}
.middot {
  font-size: 12px;
  margin: 0 8px;
}
a {
  color: #6c757d;
}
</style>
